
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/free-mode";

// import required modules
import { FreeMode, Navigation } from "swiper";

import { mapActions, mapGetters } from "vuex";
import { ProdArr } from "@/store/modules/Filter";
import { CartArr } from "@/store/modules/Cart";
import { defineComponent } from "vue";

export interface IStorageViewed {
  id: number;
}

export interface IArr {
  id: number;
  length: number;
}

export default defineComponent({
  props: ["id"],
  data() {
    return {
      isLoader: false as boolean,
      bigImage: "" as string,
      images: [] as string[],
      comparedImages: [] as string[],
      viewedProducts: [] as string[],
    };
  },

  async mounted() {
    window.scrollTo(0, 0);
    this.isLoader = true;
    // console.log("this.id", this.id);
    //await this.getSingleCat(this.cat);
    if (!isNaN(+this.id) && parseInt(this.id) > 0 && parseInt(this.id) < 101) {
      await this.getSingleProd(Number(this.id));
      await this.delSimilarImages();
      //await this.selectImages();
    } else {
      this.$router.push({ name: "error", params: { pathMatch: this.id } });
    }
    if (!this.getSingleProduct) {
      this.$router.push({ name: "error", params: { pathMatch: this.id } });
    } else {
      this.isLoader = false;
    }
    this.pushToViewed();
  },
  watch: {
    async id() {
      await this.getSingleProd(Number(this.id));
      await this.delSimilarImages();
      //await this.selectImages();

      //this.bigImage = this.getSingleProduct.thumbnail;
      window.scrollTo(0, 0);
      this.pushToViewed();
    },
  },
  computed: {
    ...mapGetters("Filter", ["getSingleProduct", "getAllProducts"]),
    ...mapGetters("Cart", ["getCartArray"]),
  },
  methods: {
    async delSimilarImages() {
      this.comparedImages = [];
      this.images = [];
      let arr: IArr[] = [];
      for (let i = 0; i < this.getSingleProduct.images.length; i++) {
        await fetch(this.getSingleProduct.images[i])
          .then((response) => response.blob())
          .then((imageBlob) => {
            //console.log(imageBlob.size);
            arr.push({ id: i, length: imageBlob.size as number });
          });
      }
      arr = arr.filter(
        (value, index, self) =>
          index === self.findIndex((t) => t.length === value.length)
      );
      // console.log("arr", arr, arr[0]);

      arr.forEach((el) => {
        for (let i = 0; i < this.getSingleProduct.images.length; i++) {
          if (el.id === i) {
            this.comparedImages.push(this.getSingleProduct.images[i]);
          }
        }
      });
      this.images = this.comparedImages;
      this.bigImage = this.images.filter((el) => el.includes("thumb"))[0]
        ? this.images.filter((el) => el.includes("thumb"))[0]
        : this.images[0];
    },
    async selectImages() {
      this.comparedImages = [];
      this.images = [];
      let arr: IArr[] = [];
      let compare: string[] = this.getSingleProduct.images;
      for (let i = 0; i < compare.length; i++) {
        await this.toDataURL(compare[i], function (dataUrl: string) {
          if (dataUrl) {
            let base = dataUrl.split(",")[1].split("=")[0];
            let strLength = base.length;
            let fileLength = strLength - (strLength / 8) * 2;
            arr.push({ id: i, length: Math.floor(fileLength) as number });
          }
        });
      }
      arr = arr.filter(
        (value, index, self) =>
          index === self.findIndex((t) => t.length === value.length)
      );
      // console.log("arr", arr, arr[0]);

      arr.forEach((el) => {
        for (let i = 0; i < this.getSingleProduct.images.length; i++) {
          if (el.id === i) {
            this.comparedImages.push(this.getSingleProduct.images[i]);
          }
        }
      });
      this.images = this.comparedImages;
      this.bigImage = this.images.filter((el) => el.includes("thumb"))[0]
        ? this.images.filter((el) => el.includes("thumb"))[0]
        : this.images[0];
    },
    toDataURL(url: string, callback: any) {
      return new Promise(function (resolve) {
        var xhr = new XMLHttpRequest();
        xhr.onload = function () {
          var reader = new FileReader();
          reader.onloadend = function () {
            resolve(callback(reader.result));
          };
          reader.readAsDataURL(xhr.response);
        };
        xhr.open("GET", url);
        xhr.responseType = "blob";
        xhr.send();
      });
    },
    ...mapActions("Filter", ["getSingleProd"]),
    ...mapActions("Cart", ["pushToCart", "delFromCart"]),
    pushToViewed() {
      if (
        localStorage.getItem("viewed") &&
        JSON.parse(localStorage.getItem("viewed") || "").length
      ) {
        let fromStorage = JSON.parse(localStorage.getItem("viewed") || "");
        this.viewedProducts = this.getAllProducts.filter((el: ProdArr) => {
          if (fromStorage.find((elem: IStorageViewed) => elem.id === el.id)) {
            return el;
          }
        });
        if (
          !fromStorage.find(
            (el: IStorageViewed) => el.id === this.getSingleProduct.id
          )
        ) {
          if (fromStorage.length >= 10) {
            fromStorage.push({ id: this.getSingleProduct.id });
            fromStorage.shift();
          } else {
            fromStorage.push({ id: this.getSingleProduct.id });
          }

          localStorage.setItem("viewed", JSON.stringify(fromStorage));
        }
      } else {
        let fromStorage = [] as IStorageViewed[];
        fromStorage.unshift({ id: this.getSingleProduct.id });
        localStorage.setItem("viewed", JSON.stringify(fromStorage));
      }
    },
    changeBigPhoto(src: string) {
      this.bigImage = src;
    },
    isBigImage(src: string) {
      return src === this.bigImage;
    },
    isActive(val: number) {
      return this.getCartArray.find((product: ProdArr) => {
        return product.id === val;
      });
    },
    async addToCart(val: CartArr) {
      val.quantity = 1;
      await this.pushToCart(val);
    },
    async delCart(val: number) {
      await this.delFromCart(val);
    },
    async openCheckout(val: CartArr) {
      val.quantity = 1;
      await this.pushToCart(val);
      this.$router.push({ name: "cart", query: { byeNow: "true" } });
    },
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [FreeMode, Navigation],
    };
  },
});
